<template>
  <div class="col-lg-8" style="width: 500px">
    <v-row class="mb-10">
      <v-col>
        <strong class="text-h3 d-flex justify-center" style="width: 100%">
          Log In
        </strong>
      </v-col>
    </v-row>
    <v-form>
      <v-col cols="12">
        <v-text-field
          label="Email or Username"
          prepend-inner-icon="mdi-account"
          v-model="username"
        ></v-text-field>
      </v-col>
      <v-col cols="12">
        <v-text-field
          :prepend-inner-icon="!password ? 'mdi-lock' : 'mdi-lock-open'"
          :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
          :type="show ? 'text' : 'password'"
          label="Password"
          @click:append="show = !show"
          v-model="password"
        ></v-text-field>
      </v-col>
      <v-col cols="12" class="d-flex align-center">
        <v-switch label="Remember me?"></v-switch>
        <v-spacer />
        <v-btn class="green white--text" @click="userLogin">Login In</v-btn>
      </v-col>
    </v-form>

    <v-dialog v-model="dialog" width="500">     
      <v-card>
        <v-card-title class="headline red lighten-2 white--text">  </v-card-title>

        <v-card-text>  </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="accent" text @click="dialog = false"> I accept </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import account from "@/api/service/account.js";

export default {
  name: "Login",
  data() {
    return {
      show: false,
      username: "",
      password: "",
      dialog: false,
      rules: {
        required: (value) => !!value || "Required.",
        min: (v) => v.length >= 8 || "Min 8 characters",
        emailMatch: () => "The email and password you entered don't match",
      },
    };
  },

  methods: {
    userLogin() {
      account
        .login(this.username, this.password)
        .then((user) => {
          this.$router.push("/"); // 
        })
        .catch((error) => {
          console.log(error);
          this.dialog = true;
        });
    },
  },
};
</script>

