import LC from 'leancloud-storage';


/**
 * @description:   
 * @param {*} username
 * @param {*} password
 * @return {*}
 */
let login = (username, password)=>{
    return new Promise((resolve, reject)=>{
        LC.User.logIn(username, password)
        .then(user=>{
            console.log(user);
            resolve(user);    // resolve() then()
        }, error=>{
            console.log(error);
            reject(error);    // reject()  catch()
        }).catch(error=>{
            reject(error); 
        });

    });
};

/**
 * @description: 
 * @param {*}
 * @return {*}
 */
let isLoggedIn=()=>{
    let currentUser = LC.User.current();
    if(currentUser){
        console.log(currentUser);
        return currentUser;  //
    }
    return false;    // false
};

/**
 * @description: 
 * @param {*}
 * @return {*}
 */
let logout = ()=>{
    LC.User.logOut();
    console.log(LC.User.current());
};

/**
 * @description:   .then()  .catch()
 * @param {*} username
 * @param {*} password
 * @param {*} email
 * @return {*}
 */
let signUp = (username, password, email='')=>{
    let user = new LC.User();    // 
    user.setUsername(username);  // 
    user.setPassword(password);  // 
    if(email){
        user.setEmail(email);
    }

    return new Promise((resolve, reject)=>{
        user.signUp()
        .then(user=>{
            console.log("");
            resolve(user);  // 
        }, error=>{
            console.log("", error);
            reject(error); // 
        })
        .catch(error=>{
            console.log(" ",error);
            reject(error);    // 
        });
    });
};



export default {
    login,
    isLoggedIn,
    logout,
    signUp,
}